.wrapper_auth_page {
  position: relative;
  width: 360px;
  margin: 0 auto;
}
.wrapper_right_side_login {
  margin-left: 100%;
  background-color: #333336 !important;
  height: 200px;
  width: 330px;
}
.form_login {
  display: flex;
  margin: 0 30px;
  margin-top: 43px;
  padding-top: 34px;
  flex-direction: column;
}
.forgot_the_paswword {
  display: flex;
  align-items: center;
}
.forgot_the_paswword span {
  color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;
  cursor: pointer;
  font-size: 14.5px;
}
.forgot_the_paswword span:hover {
  border-bottom: 1px solid #ffffff;
}
.form_login input {
  border: 1px solid #d3d9de;

  background-color: rgb(255, 255, 255);

  background-origin: padding-box;

  background-position-x: 0%;

  background-position-y: 0%;

  background-repeat: repeat;

  background-size: auto;

  border-bottom-color: rgb(211, 217, 222);

  border-bottom-left-radius: 3px;

  border-bottom-right-radius: 3px;

  border-bottom-style: solid;

  border-bottom-width: 1px;

  border-left-color: rgb(211, 217, 222);

  border-left-style: solid;
  border-left-width: 1px;

  border-right-color: rgb(211, 217, 222);

  border-right-style: solid;

  border-right-width: 1px;

  border-top-color: rgb(211, 217, 222);

  border-top-left-radius: 3px;

  border-top-right-radius: 3px;

  border-top-style: solid;

  border-top-width: 1px;

  box-shadow: none;
  box-sizing: border-box;

  color: rgb(0, 0, 0);
  direction: ltr;

  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;

  font-size: 14px;

  font-weight: 400;

  height: 35px;

  line-height: normal;
  margin-bottom: 15px;

  margin-left: 0px;

  margin-right: 0px;
  margin-top: 0px;
  outline-color: invert;

  outline-style: none;

  outline-width: 0px;
  padding-bottom: 8px;

  padding-left: 12px;
  padding-right: 12px;

  padding-top: 6px;

  text-align: left;
  width: 270px;
}
.form_login button {
  background-color: #666;
  background-origin: padding-box;

  background-position-x: 0px;
  background-position-y: 0px;

  background-repeat: repeat;

  background-size: auto;

  border-bottom-color: rgb(255, 255, 255);

  border-bottom-left-radius: 3px;

  border-bottom-right-radius: 3px;
  border-bottom-style: none;

  border-bottom-width: 0px;
  border-left-color: rgb(255, 255, 255);
  border-left-style: none;

  border-left-width: 0px;
  border-right-color: rgb(255, 255, 255);
  border-right-style: none;

  border-right-width: 0px;

  border-top-color: rgb(255, 255, 255);

  border-top-left-radius: 3px;
  border-top-right-radius: 3px;

  border-top-style: none;

  border-top-width: 0px;

  box-sizing: border-box;
  color: rgb(255, 255, 255);

  cursor: pointer;
  direction: ltr;
  display: inline-block;

  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Helvetica Neue",
    Geneva, "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee",
    "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian",
    "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao",
    "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai",
    sans-serif, arial, Tahoma, verdana;
  font-size: 14px;

  font-weight: 400;
  line-height: 20px;

  margin-bottom: 0px;

  margin-left: 0px;

  margin-right: 15px;
  margin-top: 5px;

  opacity: 0.88;

  outline-color: invert;

  outline-style: none;

  outline-width: 0px;

  padding-bottom: 8px;

  padding-left: 16px;
  padding-right: 16px;
  padding-top: 7px;

  text-align: center;

  text-decoration: none;
  vertical-align: top;

  white-space: nowrap;

  zoom: 1;
}
.form_login button:hover {
  background-color: rgb(116, 116, 116);
}
.wrapper_right_side_rightster {
  margin-top: 30px;
  margin-left: 100%;
  background-color: #333336 !important;
  height: 530px;
  width: 330px;
}
.text_top_wrapper_right_side_rightster {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.text_top_wrapper_right_side_rightster span {
  margin: 0 auto;
}
