.profile_photo {
  padding-top: 13px;
  margin-top: 22px;
  vertical-align: top;
  border-radius: 4px;
  content: "";
  max-width: 230px;
  background-color: #333336 !important;
  color: white;
  min-height: 270px;
  position: relative;
}
.profile_photo img {
  margin: 10px 14px 14px 14px;

  margin-bottom: 10px;
  margin-top: 0px;
  cursor: pointer;
}
.flex_profile_photo {
  max-width: 230px;
  display: flex;
  justify-content: center;
  margin-top: 17px;
  align-items: center;
}
.acd {
  bottom: 40px;
  position: absolute;
}
.acd button {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.acd input {
  margin: 0 auto;
  width: 110px;
}
.profile_photo_img {
  width: 205px;
  height: 205px;
}
