.wrapper_profile_friends {
  background-color: rgb(51, 51, 54);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  box-shadow: none;
  color: rgb(255, 255, 255);
  direction: ltr;
  display: block;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans",
    "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali",
    "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic",
    "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada",
    "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil",
    "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size: 13px;
  font-weight: 400;
  min-height: 95px;
  line-height: 15.002px;
  margin-bottom: 0px;
  margin-left: 0px;
  margin-right: 0px;
  width: 230px;

  margin-top: 15px;
  position: relative;
  text-align: left;
}
.top_friends_info {
  display: flex;
  justify-content: space-between;
  padding: 12px 14px;
}
.top_friends_info span {
  cursor: pointer;
}

.count_friends {
  margin-left: 9px;
  color: rgb(147, 147, 147);
}



.palka_friends {
  border-top-color: rgba(126, 124, 124, 0.247);
  border-top-style: solid;
  border-top-width: 1px;
  box-sizing: border-box;
  display: block;
  margin-top: 3px;
  margin-bottom: -5px;
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none;
}
.friends_main{
  display: flex;
}
.sp_toch_friend{
  color:rgb(255, 255, 255);
cursor:pointer;
direction:ltr;
display:inline;
font-family:-apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
font-size:12.5px !important;
font-weight:400;
display: flex;
justify-content: center;
line-height:15px;
margin-right: 20px;
text-align:center;
text-decoration-color:rgb(255, 255, 255);
text-decoration-line:none;
text-decoration-style:solid;
white-space:nowrap;

-webkit-font-smoothing:subpixel-antialiased;
}
.Wrapper_friend{
  display: flex;
  justify-content: center;

  margin-top: 0;

  flex-wrap: wrap;

}
.flex_friend_about{
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-right: 20px;
  margin-top: 0;
  justify-content: center;
  align-items: center;
  
}
.flex_friend_about:last-of-type{
}
.friends_main{
  display:flex;
  margin-right: -20px;
  justify-content: center;
}
.flex_friend_about img{
  border-bottom-color:rgba(0, 0, 0, 0);
  border-bottom-left-radius:50%;
  border-bottom-right-radius:50%;
  display: flex;
  justify-content: center;
  border-bottom-style:none;
  border-bottom-width:0px;
  border-image-outset:0;
  border-image-repeat:stretch;
  border-image-slice:100%;
  border-image-source:none;
  border-image-width:1;
  border-left-color:rgba(0, 0, 0, 0);
  border-left-style:none;
  border-left-width:0px;
  border-right-color:rgba(0, 0, 0, 0);
  border-right-style:none;
  border-right-width:0px;
  border-top-color:rgba(0, 0, 0, 0);
  border-top-left-radius:50%;
  border-top-right-radius:50%;
  border-top-style:none;
  border-top-width:0px;
  margin-bottom: 10px;
  
  color:rgba(0, 0, 0, 0);
  cursor:pointer;
  direction:ltr;
  display:inline;
  font-family:-apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", "Noto Sans Armenian", "Noto Sans Bengali", "Noto Sans Cherokee", "Noto Sans Devanagari", "Noto Sans Ethiopic", "Noto Sans Georgian", "Noto Sans Hebrew", "Noto Sans Kannada", "Noto Sans Khmer", "Noto Sans Lao", "Noto Sans Osmanya", "Noto Sans Tamil", "Noto Sans Telugu", "Noto Sans Thai", sans-serif;
  font-size:12.5px;
  font-weight:400;
  height:50px;
  line-height:14.425px;
  overflow-x:hidden;
  overflow-y:hidden;
  text-align:center;
  width:50px;
  -webkit-font-smoothing:subpixel-antialiased;
}
.flex_friend_about img:last-of-type{
 
}
.flex_friend_about span:last-of-type{
  margin-right: 0px;
}
.flex_friend_about img:first-of-type{
  margin-right: 0;
}
.asvcs:hover span{
  border-bottom:1px solid #ffffff;
}